import React, { Component } from "react";
import { Navbar, NavbarNav, NavItem, NavbarToggler, Collapse } from "mdbreact";
import Logo from "../assets/images/logo-light.png";
export default class Nav extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isWideEnough: false,
      isOpen: false
    };
    this.onClick = this.onClick.bind(this);
  }

  onClick() {
    this.setState({ isOpen: !this.state.isOpen });
  }

  handleTogglerClick = () => {
    this.setState({
      isOpen: !this.state.isOpen
    });
  };
  render() {
    const overlay = (
      <div
        id="sidenav-overlay"
        style={{ backgroundColor: "transparent" }}
        onClick={this.handleTogglerClick}
      />
    );
    return (
      <div id="apppage">
        <div>
          <Navbar
            color="mdb-color darken-3"
            transparent
            dark
            expand="md"
            fixed="top"
            scrolling
          >
            <a href="/">
              <img src={Logo} height="30" alt="" />
            </a>
            {!this.state.isWideEnough && (
              <NavbarToggler onClick={this.onClick} />
            )}
            <Collapse id="navbarCollapse3" isOpen={this.state.isOpen} navbar>
              <NavbarNav right>
                <NavItem>
                  <a href="/lodestar/#about">
                    <strong>About</strong>
                  </a>
                </NavItem>
                <NavItem>
                  <a href="/lodestar/#services">
                    <strong>Services</strong>
                  </a>
                </NavItem>
                <NavItem>
                  <a href="/lodestar/#projects">
                    <strong>Projects</strong>
                  </a>
                </NavItem>
                <NavItem>
                  <a href="/lodestar/#contact">
                    <strong>Contact</strong>
                  </a>
                </NavItem>
              </NavbarNav>
            </Collapse>
          </Navbar>
          {this.state.isOpen && overlay}
        </div>
      </div>
    );
  }
}
