import React, { Component } from "react";
import PropTypes from "prop-types";
import {
  Fa,
  Row,
  Col,
  Card,
  CardBody,
  Button,
  Input,
  Container,
  Animation
} from "mdbreact";
import SocialIcons from "../components/SocialIcons";
import { SITELINKS } from "../components/SITELINKS";
import Photo from "../assets/images/web_2.png";
class Contact extends Component {
  render() {
    return (
      <Container id="contact">
        <section className="contact-section my-5">
          <h2 className="h1-responsive font-weight-bold orange-text text-center my-5">
            Contact Us
          </h2>
          <form>
            <Card>
              <Row>
                <Col lg="8">
                  <CardBody style={{ color: "#ffa000" }} className="form">
                    <h3 className="mt-4">
                      <Fa icon="envelope" className="mr-1" />
                      Write to us:
                    </h3>
                    <Row>
                      <Col md="6">
                        <div className="md-form mb-0">
                          <Input
                            type="text"
                            id="form-contact-name"
                            label="Your name"
                            name="name"
                          />
                        </div>
                      </Col>
                      <Col md="6">
                        <div className="md-form mb-0">
                          <Input
                            type="text"
                            id="form-contact-email"
                            label="Your email"
                            name="email"
                          />
                        </div>
                      </Col>
                    </Row>
                    <Row>
                      <Col md="6">
                        <div className="md-form mb-0">
                          <Input
                            type="text"
                            id="form-contact-phone"
                            label="Your phone"
                            name="phone"
                          />
                        </div>
                      </Col>
                      <Col md="6">
                        <div className="md-form mb-0">
                          <Input
                            type="text"
                            id="form-contact-company"
                            label="Your company"
                            name="company"
                          />
                        </div>
                      </Col>
                    </Row>
                    <Row>
                      <Col md="12">
                        <div className="md-form mb-0">
                          <Input
                            type="textarea"
                            id="form-contact-message"
                            label="Your message"
                            name="message"
                          />
                          <Button rounded type="submit" color="amber darken-2">
                            <Fa icon="send-o" />
                          </Button>
                        </div>
                      </Col>
                    </Row>
                  </CardBody>
                </Col>
                <Col className="text-center" lg="4">
                  <CardBody
                    style={{ color: "#1c2a48" }}
                    className="contact h-100"
                  >
                    <Animation type="flipInY" infinite duration="4s">
                      <img src={Photo} alt="" />
                    </Animation>

                    <h4 className="my-4 pb-2">CONTACT INFORMATION</h4>
                    <ul className="text-lg-left list-unstyled ml-4">
                      <li>
                        <p>
                          <Fa icon="map-marker" className="pr-2" />
                          Istanbul - Turkey
                        </p>
                      </li>
                      <li>
                        <p>
                          <Fa icon="phone" className="pr-2" />
                          +90 539 264 83 96
                        </p>
                      </li>
                      <li>
                        <p>
                          <Fa icon="envelope" className="pr-2" />
                          ask@lodestar-soft.com
                        </p>
                      </li>
                    </ul>

                    <hr className="hr-light my-4" />
                    <ul className="list-inline text-center list-unstyled p-2 fa-lg w-ic">
                      <SocialIcons link={SITELINKS.FACEBOOK} type="facebook" />
                      <SocialIcons
                        link={SITELINKS.INSTAGRAM}
                        type="instagram"
                      />
                      <SocialIcons link={SITELINKS.LINKEDIN} type="linkedin" />
                      <SocialIcons link={SITELINKS.TWITTER} type="twitter" />
                    </ul>
                  </CardBody>
                </Col>
              </Row>
            </Card>
          </form>
        </section>
      </Container>
    );
  }
}
export default Contact;
