import React from "react";
import { Col, Container, Row, Footer, Animation, Button, Fa } from "mdbreact";
import { SITELINKS } from "./SITELINKS";
import SocialIcons from "./SocialIcons";
import Logo from "../assets/images/logo.png";
import FooterLink from "./FooterLink";
const FooterPage = () => {
  return (
    <Footer color="mdb-color darken-3" className="font-small pt-4 mt-4">
      <Container className="text-center text-md-left">
        <Row className="text-center text-md-left mt-3 pb-3">
          <Col size="3" className="white-text">
            <Animation type="slideInDown" duration="4s">
              <img style={{ height: "9em" }} src={Logo} alt="logo" />
            </Animation>
          </Col>
          <Col size="3" className="white-text">
            <h6 className="text-uppercase mb-4 font-weight-bold">
              Useful Links
            </h6>
            <p>
              <a href={SITELINKS.PROJECTS}>Our Projects</a>{" "}
            </p>
            <p>
              <a href={SITELINKS.SERVICESPAGE}>Our Services</a>{" "}
            </p>
            <p>
              <a href="/#">Our Clients</a>{" "}
            </p>
          </Col>
          <Col size="3" className="white-text">
            <h6 className="text-uppercase mb-4 font-weight-bold">Powered By</h6>
            <p>
              <a href="/#">React JS</a>{" "}
            </p>
            <p>
              <a href="/#">Mongo DB</a>{" "}
            </p>
            <p>
              <a href="/#">.net</a>{" "}
            </p>
          </Col>

          <Col size="3" className="white-text">
            <h6 className="text-uppercase mb-4 font-weight-bold">Contact</h6>
            <p>
              <i className="fa fa-home mr-3" /> Istanbul, Turkey
            </p>
            <p>
              <i className="fa fa-envelope mr-3" /> ask@lodestar-soft.com
            </p>
            <p>
              <i className="fa fa-phone mr-3" /> +90 539 264 83 96
            </p>
          </Col>
        </Row>
        <hr />
        <Row className="footer-copyright py-3 text-center">
          <Col md="8" lg="8">
            <p className="text-center text-md-left grey-text">
              &copy; {new Date().getFullYear()} Copyright:{" "}
              <a href={SITELINKS.WEBSITE} target="blank">
                {" "}
                lodestar-soft.com{" "}
              </a>
            </p>
          </Col>
          <Col md="4" lg="4" className="ml-lg-0">
            <div className="fa-lg w-ic">
              <ul className="list-unstyled list-inline">
                <li className="list-inline-item">
                  <SocialIcons link={SITELINKS.FACEBOOK} type="facebook" />
                </li>
                <li className="list-inline-item">
                  <SocialIcons link={SITELINKS.INSTAGRAM} type="instagram" />
                </li>
                <li className="list-inline-item">
                  <SocialIcons link={SITELINKS.LINKEDIN} type="linkedin" />
                </li>
                <li className="list-inline-item">
                  <SocialIcons link={SITELINKS.TWITTER} type="twitter" />
                </li>
                <li className="list-inline-item">
                  <SocialIcons link={SITELINKS.TWITTER} type="github" />
                </li>
                <li className="list-inline-item">
                  <SocialIcons link={SITELINKS.TWITTER} type="pinterest" />
                </li>
              </ul>
            </div>
          </Col>
        </Row>
      </Container>
      {/* <div className="footer-copyright py-3 text-center">
        <Container className="white-text">
          <Row>
            <Col size="3" className="white-text">
              <div className="fa-lg w-ic" />
            </Col>
          </Row>
        </Container>
      </div> */}
    </Footer>
  );
};

export default FooterPage;
