import React from "react";
import { Animation, Mask, Row, Col, View, Container, Button } from "mdbreact";
import "../assets/scss/apppage.scss";
import Nav from "../components/Nav";
import { SITELINKS } from "../components/SITELINKS";
const AppPage = () => {
  return (
    <div id="apppage">
      <div>
        <Nav />
      </div>
      <View>
        <Mask className="d-flex justify-content-center align-items-center gradient">
          <Container>
            <Row>
              <Col
                xs="4"
                sm="4"
                md="6"
                className="white-text text-center text-md-left "
                size="6"
              >
                <h1 className="h1-responsive font-weight-bold mt-sm-5">
                  LODESTAR
                </h1>
                <hr className="hr-light" />
                <div>
                  <p className="text-monospac">
                    We create powerfull web applications, design interactive
                    websites, provide strategy, business studies, and new
                    technologies to help clients succeed at the speed of
                    innovation. We invite you to learn more about the work we
                    are doing at LODESTAR.
                  </p>
                </div>
                <hr className="hr-light" />
                <Button outline color="warning" href={SITELINKS.ABOUT}>
                  Learn More
                </Button>
              </Col>

              <Col md="4" size="6">
                <Animation type="fadeInRight" duration="4s">
                  <img
                    src="https://mdbootstrap.com/img/Mockups/Transparent/Small/admin-new.png"
                    alt=""
                    className="img-fluid"
                  />
                </Animation>
              </Col>
            </Row>
          </Container>
        </Mask>
      </View>
    </div>
  );
};

export default AppPage;
